// #region quattrocento

$root-quattrocento: "../../fonts/quattrocento-sans";

@font-face {
  font-family: "Quattrocento Sans";
  src: url("#{$root-quattrocento}/quattrocento-sans-bold.woff2") format("woff2"),
    url("#{$root-quattrocento}/quattrocento-sans-bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Quattrocento Sans";
  src: url("#{$root-quattrocento}/quattrocento-sans-italic.woff2") format("woff2"),
    url("#{$root-quattrocento}/quattrocento-sans-italic.woff") format("woff");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: "Quattrocento Sans";
  src: url("#{$root-quattrocento}/quattrocento-sans-boldItalic.woff2") format("woff2"),
    url("#{$root-quattrocento}/quattrocento-sans-boldItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: "Quattrocento Sans";
  src: url("#{$root-quattrocento}/quattrocento-sans-regular.woff2") format("woff2"),
    url("#{$root-quattrocento}/quattrocento-sans-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

// #endregion

// #region americana

$root-americana: "../../fonts/americana";

@font-face {
  font-family: "Americana";
  src: url("#{$root-americana}/americana-regular.woff2") format("woff2"),
    url("#{$root-americana}/americana-regular.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

// #endregion
